.pagination {
  margin: 10px auto;
  text-align: center;
}
.pagination button {
  height: 30px;
  margin: 0 5px;
  padding: 0 15px;
  outline: none;
  background-color: transparent;
  border: solid 1px #a6a6a6;
  border-radius: 5px;
  font-size: 14px;
  cursor: pointer;
}
.pagination input {
  height: 26px;
  margin: 0 5px 0 5px;
  vertical-align: bottom;
  text-align: center;
  background-color: transparent;
  border: solid 1px #a6a6a6;
  border-radius: 5px;
  font-size: 14px;
}
